import axios from 'axios'
import Vue from 'vue';
axios.create({
    timeout:3000,
    baseURL: "http://localhost:8080/"
});
axios.interceptors.response.use(response => {
    return response.data
}, err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = ''
          break;
        case 401:
          err.message = ''
          break;
        case 403:
          err.message = ''
          break;
        case 404:
          err.message = ','
          break;
        case 405:
          err.message = ''
          break;
        case 408:
          err.message = ''
          break;
        case 500:
          err.message = ''
          break;
        case 501:
          err.message = ''
          break;
        case 502:
          err.message = ''
          break;
        case 503:
          err.message = ''
          break;
        case 504:
          err.message = ''
          break;
        case 505:
          err.message = 'http'
          break;
        default:
          err.message = `${err.response.status}`
      }
    } else {
      err.message = ""
    }
    message.err(err.message)
      return Promise.resolve(err.response)
})
Vue.prototype.$http = axios

export default axios


