<template>
  <div>
    <!--flexjustify-content -->
    <div style="display: flex;justify-content: center;margin-top: 150px">
      <el-card style="width: 375px">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <table>
          <tr>
            <td></td>
            <td>
              <el-input v-model="user.name" placeholder=""></el-input>
            </td>
          </tr>
          <tr>
          <td></td>
          <td>
            <el-input v-model="user.email" placeholder=""></el-input>
          </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <el-input type="password" v-model="user.password" placeholder="" @keydown.enter.native="doLogin"></el-input>
              <!-- @keydown.enter.native="doLogin"nteroLogin-->
            </td>
          </tr>
            <br />

          <tr>
            <!-- -->
            <td colspan="2">
              <el-button style="width: 300px" type="primary" @click="doLogin"></el-button>
              <br /> <br />
              <label><input name="accept" type="radio" value="" /> </label>
            </td>

          </tr>
        </table>
      </el-card>
    </div>
  </div>
</template>
<script>
    export default {
        data() {
            return{
                user:{
                    name:'',
                    email:'',
                    password:'',
                },
                is_login:false,
            }
        },
        methods:{
            doLogin(){//
                this.$axios.get("/login", 
				{
					params:{
							name:this.user.name,
							password:this.user.password,
				}
				})
				.then((res) => {
				 if(res=true)
				 this.is_login=true;
				 else
				 alert("");
				 this.$router.push({
    			 path: '/personal',
    			 query: {
      			    user:this.user,
      			    is_login:this.is_login,
     			 }
  				 });
				})
				.catch((error) => {
					console.log(error);
				});
            }
        },
    }
</script>




